import React, { useState } from 'react';
import AccordionItem from './AccordionItem';
import './Accordion.css';

const Accordion = () => {
  const [openIndex, setOpenIndex] = useState(null);

  const handleToggle = (index) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  const content1 = (
    <div>
      <ul>
        <li className='accordeonBtn'><a href='/worktime'> </a></li>
        <li className='accordeonBtn'><a href='/shopaddress'> </a></li>
        <li className='accordeonBtn'><a href='/service'> </a></li>
        <li className='accordeonBtn'><a href='/publicoffer'>  </a></li>
        <li className='accordeonBtn'><a href='/privacy'></a></li>
      </ul>
    </div>
  );

  const content2 = (
    <div>
      <ul>
        <li className='accordeonBtn'><a href='/shipping'> </a></li>
        <li className='accordeonBtn'><a href='/payments'>  </a></li>
        <li className='accordeonBtn'><a href='/terms'> </a></li>
        <li className='accordeonBtn'><a href='/warranty'>  </a></li>
        <li className='accordeonBtn'><a href='/return'>  </a></li>      </ul>
    </div>
  );

  const content3 = (
    <div>
      <ul>
        <li className='columnBtn'><a href='/catalog/bycicle'></a></li>
        <li className='columnBtn'><a href='/catalog/parts'></a></li>
        <li className='columnBtn'><a href='/catalog/protection'></a></li>
        <li className='columnBtn'><a href='/catalog/accessories'></a></li>
      </ul>
    </div>
  );

  return (
    <div className="accordion">
      <AccordionItem
        title=" "
        content={content1}
        isOpen={openIndex === 0}
        onToggle={() => handleToggle(0)}
      />
      <AccordionItem
        title=" "
        content={content2}
        isOpen={openIndex === 1}
        onToggle={() => handleToggle(1)}
      />
      <AccordionItem
        title=""
        content={content3}
        isOpen={openIndex === 2}
        onToggle={() => handleToggle(2)}
      />
    </div>
  );
};
export default Accordion;
